<template>
	<div class="container">
		<div class="person flex align_center space-between">
			<div class="flex align_center left">
				<div v-if="!user.photo" class="md-icon" @click="setting"></div>
				<img v-else class="md-icon" :src="user.photo" alt="" @click="setting">
				<div class="title">
					<div>{{user.username || '--'}}</div>
					<div>[{{usertypeText(user.usertype)}}]</div>
					<!-- <div style="color: #fdb731;">请假中</div> -->
				</div>
				<div class="banner">
					<h1>{{$school()}}</h1>
					<h2>{{$system()}}</h2>
				</div>
				
			</div>
		</div>
		<div style="height: calc(100vh - 23vw);overflow-y: auto;">
			<div class="md-example-child md-example-child-swiper md-example-child-swiper-2">
				<md-swiper
				  @before-change="beforeChange"
				  @after-change="afterChange"
				  :autoplay="5000"
				  transition="fade"
				  ref="swiper">
				  <md-swiper-item>
					  <img class="banner-item" src="../assets/bg.jpg" alt="">
				  </md-swiper-item>
				</md-swiper>
			</div>
			<div class="flex box">
				<div class="mode center" @click="toRealtimecheckingsleep" v-if="user.usertype != 'S'">
					<md-icon name="rectangle" size="lg"></md-icon>
					<p>实时查寝</p>
				</div>
				<div class="mode center" @click="toHistoryunback" v-if="user.usertype != 'S'">
					<md-icon name="time" size="lg"></md-icon>
					<p>历史未归</p>
				</div>
				<div class="mode center" @click="toReport" v-if="user.usertype != 'S'">
					<md-icon name="setting" size="lg"></md-icon>
					<p>宿管综合</p>
				</div>
				<div class="mode center" @click="toReport2" v-if="user.usertype != 'S'">
					<md-icon name="card-bag" size="lg"></md-icon>
					<p>学院综合</p>
				</div>
				<div class="mode center" @click="toTrafficStatistics" v-if="user.usertype != 'S'">
					<md-icon name="sort" size="lg"></md-icon>
					<p>通行统计</p>
				</div>
				<!-- <div class="mode center" @click="toPhotoUpload">
					<md-icon name="camera" size="lg"></md-icon>
					<p>照片上传</p>
				</div> -->
				<div class="mode center" @click="toSearch" v-if="user.usertype != 'S'">
					<md-icon name="search" size="lg"></md-icon>
					<p>智能查询/修改</p>
				</div>
				<div class="mode center" @click="toAddTS" v-if="user.usertype != 'S'">
					<i class="md-icon icon-font lg">+</i>
					<p>师生</p>
				</div>
				<div class="mode center" @click="toAdd" v-if="user.usertype != 'S'">
					<i class="md-icon icon-font lg">+</i>
					<p>访客</p>
				</div>
				<div class="mode center" @click="toAddYellow" v-if="user.usertype != 'S'">
					<i class="md-icon icon-font lg">+</i>
					<p>黄名单</p>
				</div>
				
				<div class="mode center" @click="toApply" v-if="user.usertype == 'S'">
					<i class="md-icon icon-font lg">+</i>
					<p>请假实习</p>
				</div>
				<div class="mode center" @click="toApplyList" v-if="user.usertype == 'S'">
					<md-icon name="sort" size="lg"></md-icon>
					<p>申请记录</p>
				</div>
				<div class="mode center" @click="toUpload" v-if="user.usertype == 'S'">
					<md-icon name="camera" size="lg"></md-icon>
					<p>上传图片</p>
				</div>
				
				<div class="mode center" @click="toNews">
					<span class="try_set_icon" v-show="count != 0">{{count}}</span>
					<md-icon name="switch" size="lg"></md-icon>
					<p>消息</p>
				</div>
				
			</div>
		</div>
		<md-dialog
			title="用户中心"
			:closable="true"
			layout="column"
			v-model="actDialog.open"
			:btns="actDialog.btns"
			>
			<div style="line-height: 5vw;">
				<div>账户名称：{{user.username || '--'}}</div>
				<div>所属院系：{{user.college_name || '--'}}</div>
				<div>手机号码：{{user.mobile || '--'}}</div>
				<div>账户类别：{{usertypeText(user.usertype)}}</div>
			</div>
		</md-dialog>
	</div>
</template>

<script>
	export default {
		name: 'home',
		data() {
			return {
				actDialog: {
					open: false,
					btns: [
						{
							text: '绑定微信',
							handler: this.removwe,
						},
						{
							text: '退出登录',
							handler: this.logout,
						},
					]
				},
				count: 0
			}
		},
		computed: {
			user() {
				return localStorage.getItem('user') ? JSON.parse(localStorage.getItem('user')) : {}
			}
		},
		methods: {
			usertypeText(type) {
				if (type == 'U') {
					return '系统管理员'
				}
				if (type == 'T') {
					return '老师'
				}
				if (type == 'S') {
					return '学生'
				}
			},
			logout() {
				this.$getL('/users/logout', {
					//
				}, (res)=>{
					this.$toast.succeed('退出成功')
					localStorage.clear()
					this.actDialog.open = false
					this.$router.push('/login')
				})
			},
			removwe() {
				if (this.user.open_id) {
					this.$getL('/remove_openid', {
						usernbr: this.user.usernbr
					}, (res)=>{
						this.$toast.succeed('解绑成功,退出重新登录')
						localStorage.clear()
						this.actDialog.open = false
						this.$router.push('/login')
					})
				} else {
					localStorage.clear()
					this.$toast.succeed('请重新登录进行绑定')
					this.$router.push('/login')
				}
			},
			setting() {
				if (this.user.open_id) {
					this.actDialog.btns[0].text = '解绑微信'
				}
				this.actDialog.open = true
			},
			toRealtimecheckingsleep() {
				this.$router.push('/realtimecheckingsleep/index')
			},
			toHistoryunback() {
				this.$router.push('/historyunback/index')
			},
			toReport() {
				this.$router.push('/report/index')
			},
			toReport2() {
				this.$router.push('/report2/index')
			},
			toSearch() {
				this.$router.push('/search/index')
			},
			toPhotoUpload() {
				this.$router.push('/photo_upload/index')
			},
			toTrafficStatistics() {
				this.$router.push('/traffic_statistics/index')
			},
			toAddTS() {
				this.$router.push('/add_ts/index')
			},
			toAdd() {
				this.$router.push('/add/index')
			},
			toAddYellow() {
				this.$router.push('/add_yellow/index')
			},
			toNews() {
				this.$router.push('/news/index')
			},
			toUpload() {
				this.$router.push('/upload/index')
			},
			toApply() {
				this.$router.push('/apply/index')
			},
			toApplyList() {
				this.$router.push('/apply/list')
			},
			//轮播图事件
			setValue(id, value) {
			  document.querySelector(id) && (document.querySelector(id).innerHTML = value)
			},
			beforeChange(from, to) {
			  this.setValue('#valueSwiper10', from)
			  this.setValue('#valueSwiper11', to)
			},
			afterChange(from, to) {
			  this.setValue('#valueSwiper12', from)
			  this.setValue('#valueSwiper13', to)
			},
			goto() {
			  this.$refs.swiper.goto(2)
			},
		},
		mounted() {
			let user = JSON.parse(localStorage.getItem('user'))
			if (!this.user.user_id) {
				this.$router.push('/login')
			} else {
				if (user.user_id) {
					if (!user.open_id) {
						let href = window.location.href;
						try{
							let openid = href.split('openid=')[1].split('#/')[0];
							this.$postL('/save_openid', {
								open_id: openid,
								usernbr: this.user.usernbr
							}, ()=>{
								let user_data = user
								user_data.open_id = openid
								localStorage.setItem('user', JSON.stringify(user_data))
								this.user.open_id = openid
								this.$toast.succeed('登录并且绑定成功！')
							})
						}catch(e){
							//TODO handle the exception
						}
					}
				}
			}
			
			//获取消息总条数
			let postParam = {}
			if (user.usertype == 'S' || user.usertype == 'T') {
				postParam.to_user_id = user.user_id
				this.$get('/news/un_read_count', postParam, (res)=>{
					this.count = res.data.count
				})
			}
			
		}
	}
</script>

