	.person {
		font-size: 3.5vw;
		padding: 4vw 4vw;
		color: #666f83;
	}
	.person .title {
		margin-left: 2vw;
		line-height: 4.5vw;
		font-size: 3.8vw;
	}
	.person .left .md-icon {
		border-radius: 1vw;
		width: 15vw;
		height: 15vw;
		box-shadow: 2px 2px 5px rgba(0,0,0,.2);
		background: url(../assets/student.png) #fff center no-repeat;
		background-size: 90% 90%;
	}
	.person .banner {
		position: absolute;
		right: 4vw;
		
		text-align: right;
	}
	.person .banner h1 {
		font-family: cursive;
		font-size: 7vw;
	}
	.person .banner h2 {
		font-size: 3vw;
	}
	.box {
		flex-wrap: wrap;
		background-color: #fff;
		padding: 2vw 0;
	}
	.box .mode {
		width: 33.33%;
		min-width: 33.33%;
		padding: 5vw 0;
		color: #666f83;
		cursor: pointer;
		position: relative;
	}
	.box .mode .mode_add {
		color: #1acfc6;
		margin-right: 0.2rem;
	}
	.box .mode .md-icon {
		color: #fff;
		border-radius: 1vw;
		width: 10vw;
		height: 10vw;
		display: block;
		text-align: center;
		line-height: 10vw;
		margin: 0 auto;
		background-color: #1acfc6;
		box-shadow: 2px 2px 5px rgba(0,0,0,.2);
	}
	.box .mode p {
		margin-top: 1vw;
