		font-weight: bolder;
	}
	.bg_img {
		position: absolute;
		width: 30vw;
		border-radius: 1vw;
		opacity: .1;
		bottom: 0px;
		left: 35vw;
	}
	.md-example-child-swiper-2 {
		height: 40vw;
	}
	.md-example-child-swiper-2 .banner-item {
		float: left;
		width: 100%;
		height: 40vw;
		font-size: 4vw;
		color: #FFF;
		box-align: center;
		align-items: center;
		box-pack: center;
		justify-content: center;
		text-decoration-line: none;
	}
	.try_set_icon {
		position: absolute;
		z-index: 1;
		color: #fff;
		background-color: red;
